.App {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 20px;
}

.App nav {
  width: 100%;
  height: 50px;
  background-color: black;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
}

.App nav a {
  margin: 20px;
  position: relative;
  top: 15px;
  color: white;
  text-decoration: none;
}

.App nav a:hover {
  color: grey;
}

.App nav a.active {
  text-decoration: underline;
}

.App nav .langbar {
  margin: 10px;
  float: right;
  color: white;
}

.App nav .langbar label {
  margin-left: 5px;
  margin-right: 20px;
}

.User, .Transactions, .Overview {
  position: relative;
  top: 50px;
}

.User label, .User input, .User button {
  margin: 10px;
}

.Transactions .Filter {
  width: 50%;
 }

.Transactions .Filter td {
   border: 0;
   padding: 10px;
 }

input[type='text'], select, input[type='number'] {
  width: 98%;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  border: 1px solid black;
  padding: 10px;
}

th, th.column-amount {
  text-align: center;
}

.column-date {
  width: 15%;
  text-align: center
}

.column-description {
  width: 35%;
}

.column-header {
  width: 30%
}

.column-amount {
  width: 15%;
  text-align: right;
}

.in {
  background-color: rgb(57, 180, 57);
  color: white;
}

.out {
  background-color: rgb(153, 39, 39);
  color: white;
}

.column-remove {
  width: 5%;
  text-align: center;
}

.show-more-button {
  margin: 20px auto;
  width: 50%;
}

.show {
  display: block;
}

.hide {
  display: none;
}

.Overview input[type='date'] {
  margin: 10px;
}

.Overview .pos {
  color: green;
}

.Overview .neg {
  color: red;
}

.Overview table {
  width: 50%;
}

.Overview .column-header {
  width: 50%;
}

.Overview .column-amount {
  width: 20%;
}

.Overview .column-percentage {
  width: 20%;
  text-align: center;
}

.Overview .column-remove {
  width: 10%;
  text-align: center;
}

.Overview label {
  margin-right: 20px;
}